// Caching the needed Dom Elements
const clockHours = window.document.querySelector('.clock-hours');
const clockMins = window.document.querySelector('.clock-mins');
const clockSecs = window.document.querySelector('.clock-secs');
const clockMode12 = window.document.querySelector('.clock-mode12');

// Helper Function to access dom to display time
const displayTime = (hours, minutes, seconds) => {
  clockMode12.textContent = hours > 11 ? 'PM' : 'AM';

  const hoursInMode12 = hours > 11 ? hours - 12 : hours;

  // clockHours.textContent =
  //   hoursInMode12 >= 10 ? hoursInMode12 : `0${hoursInMode12}`;
  // clockMins.textContent = minutes >= 10 ? minutes : `0${minutes}`;
  // clockSecs.textContent = seconds >= 10 ? seconds : `0${seconds}`;

  clockHours.textContent = `${hoursInMode12}`.padStart(2, '0');
  clockMins.textContent = `${minutes}`.padStart(2, '0');
  clockSecs.textContent = `${seconds}`.padStart(2, '0');
};

// Helper Function to calculate curent time.
const claculateTime = () => {
  const day = new Date();

  const hours = day.getHours();
  const minutes = day.getMinutes();
  const seconds = day.getSeconds();

  displayTime(hours, minutes, seconds);
};

export const clockInit = () => {
  // create periodic timer to get current time every 1000 ms = 1s
  window.setInterval(claculateTime, 1000);
};
